.lead {
	height: 100vh;
	padding: 20px 0 20px 0;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.lead__image {
	height: 500px;
}

.lead__title {
	font-size: 50px;
	font-weight: bold;
	line-height: 80px;
