	display: inline-block;
	position: relative;
	z-index: 2;
	color: #191919;
}

.lead__accent::before {
	content: '';
	position: absolute;
	background-color: #fbba06;
	width: 102%;
	height: 30px;
	z-index: -1;
	bottom: 28%;
	left: -5px;
}

.intro {
	background-color: #fbba06;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	height: 100vh;
	padding: 126px 69px 62px 69px;
}

.intro__accent {
	font-weight: bold;
}

.intro__description {
	max-width: 690px;
	font-weight: 500;
	font-size: 36px;
	line-height: 50px;
	letter-spacing: -0.015em;
	color: #191919;
}

.intro__picture {
	background-image: url(/stickermania/static/media/intro-pictire.2e055773.png);
	background-size: cover;
	width: calc((100vh - 15.7%) * 0.84);
	height: calc(100vh - 15.7%);
	background-repeat: no-repeat;
}

.issue {
	width: 100%;
	height: 100vh;
	background-image: url(/stickermania/static/media/issue-man.8409fd66.png);
	background-size: 43%;
	background-repeat: no-repeat;
	background-position: 45% 100%;
	position: relative;
}

.issue__element {
	list-style: none;
	text-decoration: none;
	font-size: 24px;
	line-height: 36px;
	letter-spacing: -0.015em;
	width: 301px;
	color: #191919;
	position: relative;
}

.issue__element_one {
	position: absolute;
	top: 440px;
	left: 320px;
}

.issue__element_two {
	position: absolute;
	top: 30px;
	left: 350px;
}

.issue__element_three {
	position: absolute;
	top: 130px;
	left: 700px;
}

.issue__element_four {
	position: absolute;
	top: 190px;
	left: 1000px;
}

.issue__element_five {
	position: absolute;
	top: 400px;
	left: 1170px;
}

.advantages {
	max-width: 100vw;
	height: 100vh;
	background-color: #fbba06;
	box-sizing: border-box;
}

.advantages__title {
	font-weight: bold;
	font-size: 48px;
	line-height: 72px;
	letter-spacing: -0.015em;
	color: #191919;
	padding: 50px 0 20px 80px;
	margin: 0;
}

.advantages__cards {
	max-width: 100%;
	margin: 0 auto;
	padding: 0 80px 0 80px;
	display: grid;
	grid-template-columns: repeat(4, 310px);
	grid-template-rows: 317px 300px;
	grid-row-gap: 20px;
	row-gap: 20px;
	grid-column-gap: 130px;
	-webkit-column-gap: 130px;
	        column-gap: 130px;
}

.advantages__card-header {
	font-weight: bold;
	font-size: 24px;
	line-height: 36px;
	letter-spacing: -0.015em;
	color: #191919;
	margin: 40px 0 0 0;
}

.advantages__card {
	list-style: none;
	max-width: 310px;
	height: 317px;
	margin: 0;
}

.advantages__description {
	margin: 5px 0 0 0;
	color: #191919;
}


