}

.feedback__button {
	margin-top: 24px;
	width: 130px;
	height: 40px;
	color: #fff;
	background-color: #18a0fb;
}

.feedback__form {
	border: 1px solid rgba(0, 0, 0, 0.5);
	background-color: #fff;
	width: 622px;
	box-sizing: border-box;
	padding: 77px 64px 24px 64px;
}

.feedback__input {
	width: 494px;
	height: 40px;
	border: 1px solid rgba(0, 0, 0, 0.5);
}

.feedback__input:invalid {
	border: 1px solid red;
}

.feedback__input:nth-child(1) {
	grid-column: 1/2;
	grid-row: 1/1;
}
.feedback__input:nth-child(2) {
	width: 237px;
	grid-column: 1/1;
	grid-row: 2/2;
}

.feedback__input:nth-child(3) {
	width: 237px;
	grid-column: 2/2;
	grid-row: 2/2;
}

.feedback__input:nth-child(4) {
	grid-column: 1/2;
	grid-row: 3/3;
}

.feedback__input:nth-child(5) {
	grid-column: 1/2;
	grid-row: 4/4;
}

.feedback__subtitle {
	font-size: 36px;
	line-height: 50px;
	letter-spacing: -0.015em;
	color: #1f1f1f;
	margin: 30px 0 80px 0;
}

.feedback__title {
	font-weight: bold;
	font-size: 48px;
	line-height: 72px;
	letter-spacing: -0.015em;
	color: #1f1f1f;
	margin: 0;
