	border: none;
	padding: 0;
	display: grid;
	grid-template-columns: repeat(2, 237px);
	grid-template-rows: repeat(4, 1fr);
	grid-row-gap: 24px;
	row-gap: 24px;
	grid-column-gap: 20px;
